@import url('https://fonts.googleapis.com/css?family=Work+Sans:400,800');

* { box-sizing: border-box; }

*, html, body { font-family: 'Work Sans', 'Helvetica', sans-serif; }

html, body { margin: 0; padding: 0; }

html {
    height: 100%;
    overflow: hidden;
}
body {
    height: 100%;
    overflow: auto;
}

.app {
  display: flex;
  flex-direction: column;
  padding-bottom: 120px;
}

header {
  display: flex;
  flex-direction: column;
  padding: 60px 0;
  margin-bottom: 30px;
}

header:after {
  content: '';
  width: 100%;
  height: 1px;

}
header .header-info {
  display: none;
}

header img {
  height: 48px;
  margin-bottom: 40px;
}

header .nav {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 40px;
}

header .nav button {
  margin: 0 20px;
  padding: 0;
  background: none;
  border: none;
  height: 100%;
  width: 40px;
  outline: none;
  transition: 0.2s;
  cursor: pointer;
}

header .nav button:hover {
  opacity: 1;
  transform: scale(1.25);
}

header .nav button:active {
  transform: scale(0.8);
}
header .nav button img {
  height: 100%;
  margin: 0;
}

header .nav button:first-child {
  margin-left: 0;
}

header .nav button:last-child {
  margin-right: 0;
}

.footer {
  position: fixed;
  bottom: 0;
  width: 100%;
  border-radius: 4px 4px 0 0;
  padding: 20px;
  text-align: center;
  background: #fff;
}
.footer a {
  color: #3333FF;
  transition: 0.25s;
  text-decoration: none;
}

.footer a:hover {
  color: #FC32BF;
}
.projects {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.project {
  display: flex;
  max-width: 440px;
  padding: 0 20px;
  flex-direction: column;
  margin-bottom: 100px;
}

.project:last-child {
  margin-bottom: 0;
}

.project img {
  width: 100%;
  border-radius: 4px;
  margin-bottom: 40px;
}

.projectsubline {
  display: flex;
  justify-content: space-between;
  width: 100%;
  overflow: visible;
}

.projectinfo {
  display: flex;
  flex-direction: column;
}

.projectname {
  color: #FC32BF;
  max-width: 180px;
  font-weight: 800;
  margin-bottom: 10px;
  font-size: 22px;
  transform:scale(1.7,1);
  transform-origin: left center;
  text-transform: uppercase;
  letter-spacing: 0.125em;
  margin-right: 0.25em;
  word-wrap:break-word;
}

.project.alt .projectname {
  color: #352AFB;
}

.projectdesc {
  color: black;
  font-size: 16px;
}

.projectbutton {
  border-radius: 4px;
  border: none;
  background: #352AFB;
  color: white;
  height: 40px;
  line-height: 40px;
  padding: 0 20px;
  text-decoration: none;
  font-weight: normal;
  transition: 0.15s linear;
}

.projectbutton:hover {
  transform: scale(1.25);
}

.projectbutton:active {
  transform: scale(0.9);
}

.project.alt .projectbutton {
  background: #FC32BF;
}



